import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    accessToken: localStorage.getItem('accessToken') ? localStorage.getItem('accessToken') : '',
    asideNavExpand: false, // 
    headerShow: true, // 
    bottomShow: true, // 
    footerShow: true, // 
    defaultAddress: localStorage.getItem('defaultAddress') ? localStorage.getItem('defaultAddress') : '', // 
    nofreight: localStorage.getItem('nofreight') ? localStorage.getItem('nofreight') : '', // 
    freight: localStorage.getItem('freight') ? localStorage.getItem('freight') : '' // 
  },
  mutations: {
    // 
    components (state, arr) {
      state.headerShow = arr[0]
      state.bottomShow = arr[1]
      state.footerShow = arr[2]
      console.log(arr)
    },
    // tokentokenlocalStorage
    changeToken (state, user) {
      state.accessToken = user.accessToken
      localStorage.setItem('accessToken', user.accessToken)
    },
    clearToken (state) {
      state.accessToken = ''
      localStorage.removeItem('accessToken')
    },
    editAddress (state, data) { // 
      state.defaultAddress = data
      localStorage.setItem('defaultAddress', JSON.stringify(data))
    }
  },
  actions: {
    clearToken ({ commit }) {
      commit('clearToken')
    }
  }
})
export default store



