function verifyPhone (val) { // 
  let valnew = (val + '').replace(/(^\s*)|(\s*$)/g, '')
  if (valnew.match(/^(13[0-9]|14[0-9]|15[0-9]|17[0-9]|18[0-9])\d{8}$/)) {
    return true
  } else {
    return false
  }
}

function verifyEmail (val) { // 
  let reg = /^[a-zA-Z0-9_-]+@([a-zA-Z0-9]+\.)+(com|cn|net|org)$/
  let res = reg.test(val)
  if (res) {
    return true
  } else {
    return false
  }
}

function timestampToTime (times) {
  if (times.lentgh > 10) { // 10*1000131000
    times *= 1000
  }
  let date = new Date(times)
  let Y = date.getFullYear() + '-'
  let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
  let D = date.getDate() + ' '
  // let h = (date.getHours() < 10 ? '0' + (date.getHours()) : date.getHours()) + ':'
  // let m = (date.getMinutes() < 10 ? '0' + (date.getMinutes()) : date.getMinutes()) + ':'
  // let s = (date.getSeconds() < 10 ? '0' + (date.getSeconds()) : date.getSeconds())
  return Y + M + D
}

let cookie = {
  set: function (key, val, time) { // cookie
    let date = new Date() // 
    let expiresDays = time // daten
    date.setTime(date.getTime() + expiresDays * 24 * 3600 * 1000) // cookie
    // document.cookie = key + '=' + val + ';expires=' + date.toGMTString();  //cookie
    document.cookie = key + '=' + val + ';expires=' + date.toGMTString() + '; path=/' // cookie
  },
  get: function (key) { // cookie
    /* cookie */
    let getCookie = document.cookie.replace(/[ ]/g, '') // cookiecookie
    let arrCookie = getCookie.split(';') // cookie'' cookiearrCookie
    let tips // tips
    for (let i = 0; i < arrCookie.length; i++) { // forcookietips
      let arr = arrCookie[i].split('=') // cookie''cookiearr
      if (key === arr[0]) { // arr[0]cookietips
        tips = arr[1] // cookietips
        break // for
      }
    }
    return tips
  },
  delete: function (key) { // cookie
    let date = new Date() // 
    date.setTime(date.getTime() - 10000) // date
    document.cookie = key + '=v; expires =' + date.toGMTString() // cookie
  }
}

// cookie
function getCookie (name) {
  let arr = null
  let reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)')
  if (arr === document.cookie.match(reg)) {
    return (arr[2])
  } else {
    return null
  }
}

// cookie,vue
function setCookie (cName, value, expiredays) {
  let exdate = new Date()
  exdate.setDate(exdate.getDate() + expiredays)
  document.cookie = cName + '=' + escape(value) + ((expiredays == null) ? '' : ';expires=' + exdate.toGMTString())
}

// cookie
function delCookie (name) {
  let exp = new Date()
  exp.setTime(exp.getTime() - 1)
  let cval = getCookie(name)
  if (cval != null) {
    document.cookie = name + '=' + cval + ';expires=' + exp.toGMTString()
  }
}

export {
  verifyPhone,
  verifyEmail,
  timestampToTime,
  cookie,
  getCookie,
  setCookie,
  delCookie
}



