console.log(this.searchValue)
},
submit () {
if (this.searchValue === '') {

this.toastShow = true
return false
}
this.searchShow = false
this.$router.push({name: 'product', params: {'pid': 0, 'val': this.searchValue}})
this.searchValue = ''
console.log('sumit')
},
link (item, index) { // 
console.log(index, item)
this.$router.push({name: item.name, params: {'pid': item.pid}})
},
searchBox () { // 
this.searchShow = !this.searchShow
},
aside () { // 
if (this.asideNav) {
this.close()
} else {
this.open()
}
},
open () { // 
this.asideNav = true

document.querySelector('html').style.cssText += 'overflow-y:hidden;'
document.querySelector('body').style.cssText += 'overflow-y:hidden;'
document.querySelector('#app').style.left = '200px'
document.querySelector('header').style.left = '200px'
},
close () { // 
this.asideNav = false
this.searchShow = false
document.querySelector('html').style.cssText += 'overflow-y:auto;'
document.querySelector('body').style.cssText += 'overflow-y:auto;'
document.querySelector('#app').style.left = 0
document.querySelector('header').style.left = 0
},
navListsSHow (navIndex) { // navBar 
this.navs.filter((item, index) => {
if (index === navIndex) {
let result = item.status = !item.status
return result
}
console.log(item, index)
})
}
}
}



