import axios from 'axios'
import store from '../store'
import router from '../router'

axios.defaults.baseURL = 'http://61.155.169.77:10002/' // 
axios.defaults.timeout = 5000
axios.defaults.headers = {
  'Content-Type': 'application/json'
}
let cancel = ''
const CancelToken = axios.CancelToken
// 
axios.interceptors.request.use(
  config => {
    if (store.state.accessToken) { // token
      config.headers['X-Access-Auth-Token'] = store.state.accessToken
    }
    return config
  }, error => {
    return Promise.reject(error)
  }
)
// 
axios.interceptors.response.use(
  response => {
    // console.log(' response', response)
    // response.data.errCode2Cookie
    /* if (response.) {
      router.push({
        path: '/login',
        query: {redirect: router.currentRoute.fullPath} // 
      })
    } */
    return response
  },
  err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break
        case 401:
          err.message = ''
          break
        case 403:
          err.message = ''
          break
        case 404:
          err.message = ','
          break
        case 405:
          err.message = ''
          break
        case 408:
          err.message = ''
          break
        case 500:
          err.message = ''
          break
        case 501:
          err.message = ''
          break
        case 502:
          err.message = ''
          break
        case 503:
          err.message = ''
          break
        case 504:
          err.message = ''
          break
        case 505:
          err.message = 'http'
          break
        default:
          err.message = `${err.response.status}`
      }
    } else {
      err.message = ''
    }
    if (err.response.status === 500 && err.response.data.message === '') {
      localStorage.removeItem('accessToken')
      router.push({
        path: '/login',
        query: {redirect: router.currentRoute.fullPath} // 
      })
    }
    return Promise.resolve(err.response)
  }
)

export default {
  get (url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'get',
        url,
        params: param,
        cancelToken: new CancelToken(c => {
          cancel = c
        })
      }).then(res => {
        resolve(res.data)
      }).catch(function (error) {
        reject(error.data)
      })
    })
  },
  post (url, param) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url,
        data: param,
        cancelToken: new CancelToken(c => {
          cancel = c
        })
      }).then(res => {
        resolve(res.data)
      }).catch(function (error) {
        reject(error.data)
      })
    })
  }
}



