import Vue from 'vue'
import Router from 'vue-router'
// import store from '../store'
import Login from '@/pages/logons/login'
import resetPassword from '@/pages/logons/resetPassword'
import changePassword from '@/pages/logons/changePassword'
import Register from '@/pages/logons/register'

import Index from '@/pages/index'
import Trail from '@/pages/trail/trail'
import Test from '@/pages/trail/test'
import Shoppe from '@/pages/shoppe/shoppe'
import Cart from '@/pages/cart/cart'
import My from '@/pages/my/my'
import Detail from '@/pages/my/detail'
import Points from '@/pages/my/points'
import Order from '@/pages/order/order'
import orderDetail from '@/pages/order/detail'
import News from '@/pages/news/news'
import newDetail from '@/pages/news/newDetail'
import Product from '@/pages/product/product'
import ProductDetail from '@/pages/product/detail'
import Settle from '@/pages/product/settle'
import Address from '@/pages/address/address'
import addressEdit from '@/pages/address/edit'

import QualityTest from '@/pages/others/qualityTest'
// 
import Story from '@/pages/others/story'
import Safe from '@/pages/others/safe'
import America from '@/pages/others/america'
import China from '@/pages/others/china'
import YunWen from '@/pages/others/yunwen'
import Facial from '@/pages/others/facial'
import Baby from '@/pages/others/baby'
import Faq from '@/pages/others/faq'

Vue.use(Router)

const router = new Router({
  scrollBehavior (to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    } // 
  },
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: Index,
      meta: {
        title: ''
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        title: ''
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        title: ''
      }
    },
    {
      path: '/resetPassword',
      name: 'resetPassword',
      component: resetPassword,
      meta: {
        title: ''
      }
    },
    {
      path: '/changePassword',
      name: 'changePassword',
      component: changePassword,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    { // type: 0()1()2()3() , id: 0()
      path: '/order/:type/:id',
      name: 'order',
      component: Order,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/orderDetail/:type/:id',
      name: 'orderDetail',
      component: orderDetail,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/news/:pid',
      name: 'news',
      component: News,
      meta: {
        title: ''
      }
    },
    {
      path: '/newDetail/:id',
      name: 'newDetail',
      component: newDetail,
      meta: {
        title: ''
      }
    },
    {
      path: '/trail',
      name: 'trail',
      component: Trail,
      meta: {
        title: ''
      },
      children: [
        {
          path: 'test',
          name: 'test',
          component: Test
        }
      ]
    },
    {
      path: '/shoppe',
      name: 'shoppe',
      component: Shoppe,
      meta: {
        title: ''
      }
    },
    {
      path: '/cart',
      name: 'cart',
      component: Cart,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/my',
      name: 'my',
      component: My,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/detail',
      name: 'detail',
      component: Detail,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/product/:pid/:val',
      name: 'product',
      component: Product,
      meta: {
        title: ''
      }
    },
    { // pid105  106
      path: '/product1/:pid/:val',
      name: 'product1',
      component: Product,
      meta: {
        title: ''
      }
    },
    {
      path: '/productjs',
      name: 'productjs',
      component: Product,
      meta: {
        title: ''
      }
    },
    { // type: 1() 2() 3()
      path: '/productDetail/:type/:id',
      name: 'productDetail',
      component: ProductDetail,
      meta: {
        title: ''
      }
    },
    { // type:1 2: 3 4:
      path: '/settle/:type',
      name: 'Settle',
      component: Settle,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/address/:type',
      name: 'address',
      component: Address,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {// type:1 () type:0 ()
      path: '/addressEdit/:id/:type',
      name: 'addressEdit',
      component: addressEdit,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/points',
      name: 'points',
      component: Points,
      meta: {
        title: '',
        requiresAuth: true
      }
    },
    {
      path: '/story',
      name: 'story',
      component: Story,
      meta: {
        title: ''
      }
    },
    {
      path: '/safe',
      name: 'safe',
      component: Safe,
      meta: {
        title: ''
      }
    },
    {
      path: '/america',
      name: 'america',
      component: America,
      meta: {
        title: 'Belli'
      }
    },
    {
      path: '/china',
      name: 'china',
      component: China,
      meta: {
        title: 'Belli'
      }
    },
    {
      path: '/yunwen',
      name: 'yunwen',
      component: YunWen,
      meta: {
        title: ''
      }
    },
    {
      path: '/facial',
      name: 'facial',
      component: Facial,
      meta: {
        title: ''
      }
    },
    {
      path: '/baby',
      name: 'baby',
      component: Baby,
      meta: {
        title: ''
      }
    },
    {
      path: '/faq',
      name: 'faq',
      component: Faq,
      meta: {
        title: ''
      }
    },
    {
      path: '/qualityTest',
      name: 'qualityTest',
      component: QualityTest,
      meta: {
        title: ''
      }
    }
  ]
})

//  
router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    let token = localStorage.getItem('accessToken')
    if (!token) {
      next('/login')
    } else {
      next()
    }
  } else {
    next()
  }
  // window.scrollTo(0, 0)  // 
  if (to.meta.title) {
    /* title */
    document.title = to.meta.title
  }
})

export default router



